<template>
<div class="random-image">
  <div class="info">
    <div class="headline">
      {{headline}}
    </div>
    <div class="copy">
      {{copy}}
    </div>
  </div>
  <button class="refresh" @click="refresh">Under Construction!</button>
  <div class="image">
    <div v-for="(pixel, index) in pixels" :style="pixel" :key="index" class="pixel" />
  </div>
</div>
</template>

<script>
export default {
  name: "RandomImageGeneratorComponent",
  props: {
    headline: {
      type: String,
      required: true
    },
    copy: {
      type: String,
      required: true
    },
    width: {
      type: Number,
      default: 500
    },
    height: {
      type: Number,
      default: 500
    }
  },
  data() {
    return {
      pixels: []
    }
  },
  methods: {
    randomPixels() {
      let pixels = []
      for (let i = 0; i < 400; i++) {
        for (let j = 0; j < 400; j++) {
          let randomBackground = this.randomBlackWhiteColor()
          let style = 'background-color: ' + randomBackground + ';'
          pixels.push(style)
        }
      }
      return pixels
    },
    randomBlackWhiteColor() {
      let random = Math.floor(Math.random() * 100)
      if (random === 1) {
        return 'white'
      }
      return 'black'
    },
    randomColor() {
      let letters = '0123456789ABCDEF';
      let color = '#';
      for (let i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
      }
      return color;
    },
    refresh() {
      //this.pixels = this.randomPixels()
    }
  },
  created() {
    //this.refresh()
  }
}
</script>

