<template>
  <div class="logo-copy">
    <div class="logo">
      <img alt="logo" :src="require('@/assets/img/' + logo + '.png')" />
    </div>
    <div class="copy">
      {{animatedCopy}}
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";

export default {
  name: "LogoCopyComponent",
  props: {
    logo: {
      type: String,
      required: true
    },
    copy: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      animatedCopy: ''
    }
  },
  computed: {
    ...mapGetters(['settings'])
  },
  mounted() {
    if (this.settings.animateCopies) {
      let i = 0
      setInterval(function () {
        this.animatedCopy = this.animatedCopy + this.copy.charAt(i)
        i = i + 1
      }.bind(this), 50)
    } else {
      this.animatedCopy = this.copy;
    }
  }
}
</script>

<style scoped lang="scss">
.logo-copy {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  margin-top: 10px;
  margin-bottom: 10px;

  .logo {
    img {
      width: 50px;
      height: 50px;
    }
  }
}
</style>