<template>
  <div class="ascii">
    <div class="info">
      <div class="headline">
        {{headline}}
      </div>
      <div class="copy">
        {{copy}}
      </div>
    </div>
    <div class="ascii-input">
      <input v-model="input" class="input" type="text" />
    </div>
    <div class="ascii-input">
      <textarea v-model="asciiForInput" class="output" disabled />
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  name: "AsciiGeneratorComponent",
  props: {
    headline: {
      type: String,
      required: true
    },
    copy: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      input: 'abc'
    }
  },
  computed: {
    ...mapGetters(['ascii']),
    asciiForInput() {
      let output = []

      for (let letter of this.input) {
        let lines = this.ascii.letters[letter].split('\n')
        let offset = 0

        //get largest line
        for (let i = 0; i < lines.length; i++) {
          if (lines[i].length > offset) {
            offset = lines[i].length
          }
        }
        for (let i = 0; i < lines.length; i++) {
          let lineOffset = offset - lines[i].length
          lines[i] = lines[i] + " ".repeat(lineOffset)
        }

        for (let i = 0; i < lines.length; i++) {
          output[i] = (output[i] === undefined ? '' : output[i]) + lines[i]
        }
      }

      return output.join('\n')
    }
  },
  methods: {}
}
</script>

