<template>
  <div class="page" :class="[isMobile === true ? 'mobile' : '']">
    <HeaderComponent />
    <component
        v-for="(component, index) in content"
        :is="component.type"
        :data="component"
        :key="index"
        v-bind="component.props" />
    <FooterComponent />
  </div>
</template>

<script>
import HeaderComponent from "@/components/HeaderComponent";
import AboutComponent from "@/components/AboutComponent";
import ProjectComponent from "@/components/ProjectComponent";
import DigitalArtComponent from "@/components/DigitalArtComponent";
import FooterComponent from "@/components/FooterComponent";
import Bowser from "bowser";
import store from "@/store/store";
import config from './/assets/data/data.json'
import {mapGetters} from "vuex";

export default {
  name: 'App',
  components: {
    HeaderComponent,
    AboutComponent,
    ProjectComponent,
    DigitalArtComponent,
    FooterComponent
  },
  data() {
    return {
      content: config.content
    }
  },
  computed: {
    ...mapGetters(['isMobile'])
  },
  created() {
    // load cfg
    store.commit('setConfig', config)

    // set title
    document.title = config.meta.title;

    // check if mobile device
    let isMobile = Bowser.parse(window.navigator.userAgent).platform.type;
    if (isMobile === 'mobile') {
      store.commit('isMobile', true)
    }
  }
}
</script>

<style lang="scss">
body, html {
  margin: 0;
  color: #161b22 !important;
  font-family: 'Courier New', monospace;
}

.page {
  position: relative;
  height: 100%;
  margin-bottom: 100px;
  padding-left: 10%;
  padding-right: 10%;

  &.mobile {
    padding-left: 20px;
    padding-right: 20px;
  }
}
</style>
