<template>
  <div class="image-collection" :class="isMobile ? 'mobile' : ''">
    <div class="wrapper">
      <div v-if="isMobile === false" class="arrow arrow-left" @click="previousSlide">
        <img alt="arrow-left" src="../assets/img/arrow-left.svg" />
      </div>
      <div class="images" @click="nextSlide">
        <div v-for="(image, index) in imagesForSlide" :key="index" class="image">
          <img alt="image" :src="require('@/assets/img/' + image.src)" />
        </div>
      </div>
      <div v-if="isMobile === false" class="arrow arrow-right" @click="nextSlide">
        <img alt="arrow-right" src="../assets/img/arrow-right.svg" />
      </div>
    </div>
    <div class="dots">
      <div v-for="(slide, index) in slideLength" :key="index" class="dot" :class="index === currentSlide ? 'active' : ''" @click="setSlide(index)" />
    </div>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  name: "ImageCollectionComponent",
  props: {
    images: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      currentSlide: 0
    }
  },
  computed: {
    ...mapGetters(['isMobile']),
    slideLength() {
      return Math.ceil(this.images.length / (this.isMobile ? 1 : 3))
    },
    imagesForSlide() {
      let images = []
      for (let i = 0; i < (this.isMobile ? 1 : 3); i++) {
        let j = i + this.currentSlide * (this.isMobile ? 1 : 3)
        if (j < this.images.length) {
          images.push(this.images[j])
        }
      }
      return images
    }
  },
  methods: {
    nextSlide() {
      if (this.currentSlide + 1 < this.slideLength) {
        this.currentSlide = this.currentSlide + 1
      }
    },
    previousSlide() {
      if (this.currentSlide > 0) {
        this.currentSlide = this.currentSlide - 1
      }
    },
    setSlide(index) {
      this.currentSlide = index
    }
  }
}
</script>

<style scoped lang="scss">
.image-collection {
  display: flex;
  flex-direction: column;
  align-items: center;

  .wrapper {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    width: 100%;

    .images {
      display: flex;
      flex-direction: row;
      gap: 20px;
      width: 100%;
      height: 100%;

      .image {
        position: relative;
        width: 33%;
        height: 500px;
        overflow: hidden;
        border: 1px solid lightgrey;
        object-fit: cover;

        img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          object-fit: cover;
        }
      }
    }
  }
  .dots {
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-top: 10px;

    .dot {
      height: 20px;
      width: 20px;
      border-radius: 50%;
      background-color: lightgrey;

      &.active {
        background-color: grey;
      }
    }
  }

  &.mobile {
    .wrapper {
      .images {
        .image {
          height: 250px;
          width: 100%;
        }
      }
    }
  }
}
</style>