<template>
  <div class="image-slider" :class="isMobile ? 'mobile' : ''">
    <div v-if="isMobile === false" class="arrow arrow-left" @click="previousImage">
      <img alt="arrow-left" src="../assets/img/arrow-left.svg" />
    </div>
    <div class="wrapper">
      <div class="headline">
        {{image.headline}}
      </div>
      <div class="image" @click="nextImage">
        <img :src="require('@/assets/img/' + image.src)" />
      </div>
      <div class="copy">
        {{animatedCopy}}
      </div>
      <div class="dots">
        <div
            v-for="(image, index) in images"
            :key="index" class="dot"
            :class="index === currentImage ? 'active' : ''"
            @click="imageByIndex(index)">
        </div>
      </div>
    </div>
    <div v-if="isMobile === false" class="arrow arrow-right" @click="nextImage">
      <img alt="arrow-right" src="../assets/img/arrow-right.svg" />
    </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
export default {
  name: "ImageSliderComponent",
  props: {
    images: {
      type: Array,
      required: true
    },
    animate: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      currentImage: 0,
      animatedCopy: ''
    }
  },
  computed: {
    image() {
      return this.images[this.currentImage]
    },
    ...mapGetters(['settings', 'isMobile'])
  },
  mounted() {
    this.animateCopy()
  },
  methods: {
    dragStart(event) {
      console.log('dragStart', event)
    },
    dragStop(event) {
      console.log('dragStop', event)
    },
    previousImage() {
      if (this.currentImage > 0) {
        this.currentImage = this.currentImage - 1
      } else {
        this.currentImage = this.images.length - 1;
      }
      this.animateCopy()
    },
    nextImage() {
      if (this.currentImage <= (this.images.length+1 - this.currentImage)) {
        this.currentImage = this.currentImage + 1;
      } else {
        this.currentImage = 0;
      }
      this.animateCopy()
    },
    imageByIndex(index) {
      this.currentImage = index
    },
    animateCopy() {
      if (this.settings.animateCopies && this.animate) {
        this.animatedCopy = ''
        let i = 0
        setInterval(function () {
          this.animatedCopy = this.animatedCopy + this.image.copy.charAt(i)
          i = i + 1
        }.bind(this), 50)
      } else {
        this.animatedCopy = this.image.copy
      }
    }
  }
}
</script>

