import Vuex from 'vuex'
export default new Vuex.Store({
    state: {
        config: {},
        isMobile: false,
        location: '#about'
    },
    getters: {
        isMobile: (state) => {
            return state.isMobile
        },
        settings: (state) => {
            return state.config.settings
        },
        config: (state) => {
            return state.config
        },
        meta: (state) => {
            return state.config.meta
        },
        menu: (state) => {
            return state.config.menu
        },
        footer: (state) => {
            return state.config.footer
        },
        content: (state) => {
            return state.config.content
        },
        ascii: (state) => {
            return state.config.ascii
        }
    },
    mutations: {
        setConfig(state, config) {
            state.config = config
        },
        isMobile(state, isMobile) {
            state.isMobile = isMobile
        },
    },
})