<template>
<div class="ascii-random-art">
  <div class="info">
    <div class="headline">
      {{headline}}
    </div>
    <div class="copy">
      {{copy}}
    </div>
  </div>
  <button class="refresh" @click="refresh">Refresh</button>
  <div class="random-art">
    <!-- <textarea v-model="art" class="art" disabled /> -->
    <div class="art">
      {{art}}
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: "AsciiRandomArtComponent",
  props: {
    headline: {
      type: String,
      required: true
    },
    copy: {
      type: String,
      required: true
    },
    width: {
      type: Number,
      default: 1000
    },
    height: {
      type: Number,
      default: 1000
    }
  },
  data() {
    return {
      art: ''
    }
  },
  methods: {
    randomArt() {
      let randomArt = ''
      for (let i = 0; i < this.width; i++) {
        for (let j = 0; j < this.height; j++) {
          if (Math.floor(Math.random() * 1000) === 1) {
            randomArt += String.fromCharCode(Math.floor(Math.random() * (47 - 32 + 1) + 32))
          } else {
            let randomChar = Math.floor(Math.random() * 6)
            switch (randomChar) {
              case 1: randomArt += '-'; break;
              case 2: randomArt += '/'; break;
              case 3: randomArt += '|'; break;
              case 4: randomArt += '\\'; break;
              case 5: randomArt += '_'; break;
              case 6: randomArt += ' '; break;
            }
          }
        }
        randomArt += '\n'
      }
      return randomArt
    },
    refresh() {
      this.art = this.randomArt()
    }
  },
  created() {
    this.refresh()
  }
}
</script>

