<template>
  <div class="skill" :class="flipped ? 'flip' : ''" v-bind:style="{ 'border-color': borderColor }" @click="flipSkill">
    <div v-if="!flipped" class="item">
      {{ skill }}
    </div>
    <div v-if="flipped" class="item">
      {{ percentage }}
    </div>
  </div>
</template>

<script>
export default {
  name: "SkillComponent",
  props: {
    skill: {
      type: String,
      required: true
    },
    percentage: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      flipped: false
    }
  },
  computed: {
    reversedPercentage() {
      return this.percentage.toString().split("").reverse().join("")
    },
    borderColor() {
      let color = 'red'
      if (this.percentage >= 25 && this.percentage < 50) {
        color = 'orange'
      }
      if (this.percentage >= 50 && this.percentage < 75) {
        color = '#fad02c'
      }
      if (this.percentage >= 75) {
        color = 'green';
      }
      return color
    }
  },
  methods: {
    flipSkill() {
      this.flipped = !this.flipped
    }
  }
}
</script>

<style scoped lang="scss">
.skill {
  position: relative;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  border: 4px solid;
  cursor: pointer;

  &.flip {
    transform: rotateY(180deg);
    transition: 1s ease-in-out;

    .item {
      transform: rotateY(180deg);
      transition: 2s ease-in-out;
    }
  }

  .item {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 12px;
    font-weight: bold;
  }
}
</style>